.card {
  display: flex;
  flex-direction: column;

  padding: 32px 16px 16px;

  color: $color-dark;
  background-color: $color-white;

  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__image {
    height: 134px;
    align-self: center;
    margin-bottom: 40px;
  }

  &__name {
    font-weight: 500;
    line-height: 18px;
    font-size: 12px;
    margin-bottom: 4px;
  }

  &__code {
    @extend %small-text;
    color: $color-grey;

    margin-bottom: $card-gap;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-bottom: 24px;
  }

  &__stars {
    margin-right: 17px;
  }

  &__reviews {
    font-weight: 400;
    @extend %small-text;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    margin-bottom: $card-gap;
  }

  &__price-text {
    line-height: $price-height;
    color: $color-grey;
  }

  &__price-number {
    font-weight: 700;
    font-size: 16px;
    line-height: $price-height;
  }

  &__buy {
    height: 38px;
    display: block;
    text-align: center;

    background-color: $color-blue;
    border: 1px solid transparent;
    border-radius: 5px;

    font-weight: 700;
    font-size: 14px;
    line-height: 40px;
    text-transform: uppercase;
    color: $color-white;
    text-decoration: none;

    &:hover {
      height: 37px;
      background-color: $color-white;
      border-color: $color-blue;
      color: $color-blue;
    }
  }
}
